import React, { useEffect, useRef, useState } from 'react';
import './mapstyle.css';
const { kakao } = window;

const MapComponent = () => {
  const [openOverlayIndex, setOpenOverlayIndex] = useState(null);
  const overlays = useRef([]);
  const mapRef = useRef(null);

  const closeOverlay = () => setOpenOverlayIndex(null);

  useEffect(() => {
    const mapContainer = document.getElementById('map');
    const mapOption = {
      center: new kakao.maps.LatLng(37.515524, 127.045115),
      level: 5,
    };

    const map = new kakao.maps.Map(mapContainer, mapOption);

    mapRef.current = map;
    const positions = [
      {
        content:
            '<div class="wrap">' + 
            '    <div class="info">' + 
            '        <div class="title">' + 
            '           롯데캐슬프레미어아파트' + 
            '            <div class="close" title="닫기"></div>' + 
            '        </div>' + 
            '        <div class="body">' + 
            '            <div class="img">' +
            '                <img src="data:image/jpeg;base64," width="73" height="70">' +
            '           </div>' + 
            '            <div class="desc">' + 
            '                <div class="ellipsis">서울특별시 강남구 선릉로126길 22</div>' +  
            '                <div><a href="/D_1" class="link">예약확인</a></div>' + 
            '            </div>' + 
            '        </div>' + 
            '    </div>' +    
            '</div>',
        latlng: new kakao.maps.LatLng(37.515524, 127.045115)
      },
      {
        content: 
        '<div class="wrap">' + 
        '    <div class="info">' + 
        '        <div class="title">' + 
        '           롯데캐슬킹덤아파트' + 
        '            <div class="close" title="닫기"></div>' + 
        '        </div>' + 
        '        <div class="body">' + 
        '            <div class="img">' +
        '                <img src="data:image/jpeg;base64," width="73" height="70">' +
        '           </div>' + 
        '            <div class="desc">' + 
        '                <div class="ellipsis">서울특별시 강남구 삼성동 43</div>' + 
        '                <div><a href="/D_2" class="link">예약확인</a></div>' + 
        '            </div>' + 
        '        </div>' + 
        '    </div>' +    
        '</div>',
        latlng: new kakao.maps.LatLng(37.513134,127.047798)
      },
      {
        content: 
        '<div class="wrap">' + 
        '    <div class="info">' + 
        '        <div class="title">' + 
        '           삼성대우멤버스카운티1차' + 
        '            <div class="close" title="닫기"></div>' + 
        '        </div>' + 
        '        <div class="body">' + 
        '            <div class="img">' +
        '                <img src="data:image/jpeg;base64," width="73" height="70">' +
        '           </div>' + 
        '            <div class="desc">' + 
        '                <div class="ellipsis">서울특별시 강남구 삼성동 98-18</div>' + 
        '                <div><a href="/D_3" class="link">예약확인</a></div>' + 
        '            </div>' + 
        '        </div>' + 
        '    </div>' +    
        '</div>',
        latlng: new kakao.maps.LatLng(37.518151,127.064566)
      },
      {
        content: 
        '<div class="wrap">' + 
        '    <div class="info">' + 
        '        <div class="title">' + 
        '            삼성대우멤버스카운티2차' + 
        '            <div class="close" title="닫기"></div>' + 
        '        </div>' + 
        '        <div class="body">' + 
        '            <div class="img">' +
        '                <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT8iUG8nDMgLtIAsQb5QpUuzJfN14c6a2WThQ&usqp=CAU" width="73" height="70">' +
        '           </div>' + 
        '            <div class="desc">' + 
        '                <div class="ellipsis">서울특별시 강남구 삼성동 33-20</div>' + 
        '                <div><a href="/D_4" class="link">예약확인</a></div>' + 
        '            </div>' + 
        '        </div>' + 
        '    </div>' +    
        '</div>',
        latlng: new kakao.maps.LatLng(37.513143,127.044239)
      },
      {
        content: 
        '<div class="wrap">' + 
        '    <div class="info">' + 
        '        <div class="title">' + 
        '            노블테라스아파트' + 
        '            <div class="close" title="닫기"></div>' + 
        '        </div>' + 
        '        <div class="body">' + 
        '            <div class="img">' +
        '                <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRDlw4NBkwFj9bRPay97-asaKYb6APOXeItfQ&usqp=CAU" width="73" height="70">' +
        '           </div>' + 
        '            <div class="desc">' + 
        '                <div class="ellipsis">서울특별시 강남구 삼성동 112  노블테라스</div>' + 
        '                <div><a href="/D_5" class="link">예약확인</a></div>' + 
        '            </div>' + 
        '        </div>' + 
        '    </div>' +    
        '</div>',
        latlng: new kakao.maps.LatLng(37.510244,127.064710)
      },


    ];


    positions.forEach((position, index) => {
      const marker = new kakao.maps.Marker({
        map: map,
        position: position.latlng,
      });

      const overlay = new kakao.maps.CustomOverlay({
        content: position.content.replace('title="닫기"', `title="닫기" onclick="window.closeOverlay(${index})"`),
        map: null,
        position: position.latlng,
      });

      overlays.current[index] = overlay;

      kakao.maps.event.addListener(marker, 'click', () => {
        setOpenOverlayIndex(index);
      });
    });

    window.closeOverlay = (index) => {
      overlays.current[index].setMap(null);
    };
  }, []);

  useEffect(() => {
    overlays.current.forEach((overlay, index) => {
      if (index === openOverlayIndex) {
        overlay.setMap(mapRef.current);
      } else {
        overlay.setMap(null);
      }
    });
  }, [openOverlayIndex]);

  return (
    <div id="map" style={{ width: '100%', height: '740px' }}></div>
  );
};

export default MapComponent;